<template>
    <yd-layout>
        <div class="empAccept">
            <div class="line" @click="clickInfo(1)">
                <div :class="{lineACtive2:lineNum==1}">问题分配</div>
                <div class="selecter" v-show="selecter!=''&&lineNum==1">{{selecter}}</div>
            </div>
            <div class="line" @click="clickInfo(2)">
                <div :class="{lineACtive2:lineNum==2}">问题转交</div>
                <div class="selecter" v-show="selecter2!=''&&lineNum==2">{{selecter2}}</div>
            </div>
            <div class="line" :class="{lineACtive:lineNum==3}" @click="clickInfo(3)">完成反馈</div>
        </div>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">反馈结果</div>
            <yd-cell-group class="otherInput">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="opinion" placeholder="请您输入反馈结果..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="no" @click="show=false">取消</li>
                    <li class="yes" @click="comfireSend()">确认</li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="zjPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="zjPopup = false">取消</div>
                <div class="rightBtn" v-on:click="zjPopup = false;lineNum=2">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" :class="{active:zjRadio==1}" @click="zjRadio=1;selecter2=chuliIDs=leaderName_a;zhuBanIDs=leaderIDs_a">无法登录/绑卡/注册/显示二维码（{{leaderName_a}}）
                    </li>
                    <li class="li" :class="{active:zjRadio==2}" @click="zjRadio=2;selecter2=chuliIDs=leaderName_b;zhuBanIDs=leaderIDs_b">支付失败（{{leaderName_b}}）
                    </li>
                    <li class="li" :class="{active:zjRadio==3}" @click="zjRadio=3;selecter2=chuliIDs=leaderName_c;zhuBanIDs=leaderIDs_c">无法享受优惠（{{leaderName_c}}）
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-show="zjPopup==false">
            <div class="agree" @click="$router.go(-1)">返回</div>
            <div class="back" @click="comfireSend()">确认发送</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'fdAgent',
    data() {
        return {
            lineNum: -1,
            selecter: '',
            selecter2: '',
            zhuBanIDs: '',
            chuliIDs: '',
            show: false,
            opinion: '',
            zjPopup: false,
            leaderName_a: '',
            leaderIDs_a: '',
            leaderName_b: '',
            leaderIDs_b: '',
            leaderName_c: '',
            leaderIDs_c: '',
            zjRadio: -1,
            leaderIDs: this.$route.params.leaderIDs
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('营销问题反馈');
        vm.select();
        vm.queryLeader('a');
        vm.queryLeader('b');
        vm.queryLeader('c');
        vm.zhuBanIDs = vm.getSearchInfo.selEmpCode.join(',');
        // vm.chuliIDs = vm.getSearchInfo.selUserId.join(',');
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getSearchInfo', 'getFeedbackInfo'])
    },
    methods: {
        ...mapMutations([
            'setFeedbackInfo', 'setSearchInfo'
        ]),
        select() {
            let vm = this;
            vm.lineNum = vm.getFeedbackInfo.lineNum;
            // vm.getSearchInfo.selectPerson.map((item) => {
            //     vm.selecter += item.empname + '、';
            //     vm.chuliIDs += item.empname + ',';
            // })
            vm.selecter = vm.getSearchInfo.selUserName.toString();
            vm.chuliIDs = vm.getSearchInfo.selUserName.toString();
            // vm.chuliIDs = vm.chuliIDs.substring(0, vm.chuliIDs.length - 1);
        },
        comfireSend() {
            let vm = this;
            if (vm.lineNum === 1 || vm.lineNum === 2) {
                if (vm.selecter == '' && vm.lineNum === 1) {
                    vm.$dialog.toast({
                        mes: '请选择分配人员',
                        timeout: 2000
                    });
                } else if (vm.selecter2 == '' && vm.lineNum === 2) {
                    vm.$dialog.toast({
                        mes: '请选择转交人员',
                        timeout: 2000
                    });
                } else {
                    vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/avertWTCL', {
                        'workItemID': vm.$route.params.workId,
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'zhuBanIDs': vm.zhuBanIDs,
                        'personID': vm.getUserInfo.empcode,
                        'workTitle': vm.getFeedbackInfo.jsonData.reqdeptname + vm.getFeedbackInfo.jsonData.requsername + '营销问题反馈',
                        'chuliIDs': vm.chuliIDs
                    }, function(res) {
                        if (res.isSuccess == 'true') {
                            vm.setSearchInfo({
                                selUserName: [],
                                selEmpCode: [],
                                selUserId: []
                            });
                            vm.setFeedbackInfo({
                                lineNum: -1
                            });
                            vm.$dialog.toast({
                                mes: '发送成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    });
                }
            } else if (vm.lineNum === 3) {
                vm.show = true;
                if (vm.opinion == '') {
                    vm.$dialog.toast({
                        mes: '请输入反馈结果',
                        timeout: 2000
                    });
                } else {
                    vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                        'type': 'troubleshootingMain',
                        'workItemID': vm.$route.params.workId,
                        'personID': vm.getUserInfo.userid,
                        'activityID': 'resultConfirmActivity',
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'leaderIDs': vm.leaderIDs,
                        'workTitle': vm.getFeedbackInfo.jsonData.reqdeptname + vm.getFeedbackInfo.jsonData.requsername + '营销问题反馈',
                        'opinion': vm.opinion
                    }, function(res) {
                        if (res.isSuccess == 'true') {
                            vm.$dialog.toast({
                                mes: '发送成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    })
                }
            } else {
                vm.$dialog.toast({
                    mes: '请选择发送选项',
                    timeout: 2000
                });
            }
        },
        clickInfo(num) {
            let vm = this;
            if (num == 1) {
                vm.lineNum = 1;
                vm.$router.push('/selectPerson');
                vm.setFeedbackInfo({
                    lineNum: vm.lineNum
                })
            } else if (num == 2) {
                vm.zjPopup = true;
                vm.setFeedbackInfo({
                    lineNum: vm.lineNum
                })
            } else if (num == 3) {
                vm.show = true;
                vm.lineNum = 3;
            }
        },
        queryLeader(type) {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryWTCLHandlerPerson', {
                'queryType': type,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                if (type === 'a') {
                    let arrAname = [];
                    let arrAid = [];
                    res.jsonData.map((item) => {
                        arrAname.push(item.empname);
                        arrAid.push(item.empcode);
                    });
                    vm.leaderName_a = arrAname.join(',');
                    vm.leaderIDs_a = arrAid.join(',');
                } else if (type === 'b') {
                    let arrBname = [];
                    let arrBid = [];
                    res.jsonData.map((item) => {
                        arrBname.push(item.empname);
                        arrBid.push(item.empcode);
                    });
                    vm.leaderName_b = arrBname.join(',');
                    vm.leaderIDs_b = arrBid.join(',');
                } else if (type === 'c') {
                    let arrCname = [];
                    let arrCid = [];
                    res.jsonData.map((item) => {
                        arrCname.push(item.empname);
                        arrCid.push(item.empcode);
                    });
                    vm.leaderName_c = arrCname.join(',');
                    vm.leaderIDs_c = arrCid.join(',');
                }
            })
        }
    }
}
</script>
<style lang="less" scoped>
// 底部按钮
.agree,
.back {
    flex: 1;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
}

.agree {
    background-color: #59a5fd;
}

.back {
    background-color: #8dce3f;
}

.empAccept {
    min-height: 100%;
    background-color: #fff;

    .line {
        min-height: 1.1rem;
        line-height: 1.1rem;
        font-size: .32rem;
        color: #333;
        padding: 0 .45rem;
        border-bottom: 1px solid #eaeaea;
    }

    .selecter {
        line-height: .6rem;
        padding-bottom: .2rem;
    }

    .lineACtive {
        background: url(../../../static/images/workCenter/20.png) no-repeat;
        background-position: 6.5rem center;
    }

    .lineACtive2 {
        background: url(../../../static/images/workCenter/20.png) no-repeat;
        background-position: right center;
    }
}
</style>


